* {
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}
.page {
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  width: 100vw;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
}

.user {
  display: flex;
  flex-direction: column;
  margin: auto;
  align-items: center;

  &__avatar {
    width: 320px;
    height: 320px;
    border-radius: 50%;
  }

  &__info {
    text-align: center;
    font-size: 24px;
    margin: 32px;
  }

  &__name {
    font-weight: 700;
  }

  &__location {
    opacity: 0.5;
    margin-left: 8px;
  }
}

.name-form {
  margin: 32px auto;

  &__input {
    height: 32px;
    border: 3px solid #ccc;
    border-radius: 8px;
    flex: 1;
    outline: none;
  }
}

.btn {
  height: 40px;
  min-width: 64px;
  border: 3px solid #ccc;
  outline: none;
  cursor: pointer;
  border-radius: 8px;
  margin-left: 16px;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
.spinner {
  display: block;
  margin: auto;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: 4px solid black;
  border-bottom-color: transparent;
  animation: rotate 1s linear infinite;
}
